import * as React from "react"
import { Link, graphql, useStaticQuery } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"
import BackgroundImage from 'gatsby-background-image'

import Layout from "../components/layout"
import FullScreenSection from "../components/full-screen-section"
import Seo from "../components/seo"

import styled from 'styled-components'

import { Suspense , useRef, useState, useMemo, useEffect } from 'react';

import * as d3 from 'd3';
// import tree from 'd3-tree';

import { textwrap } from 'd3-textwrap';

import { Modal } from 'bootstrap';

const RepositorioPage = () => {

  const data = useStaticQuery(
    graphql`
      query {
        bg1:

        file(relativePath: { eq: "pointcloud2.jpg" }) {
          childImageSharp {
            fluid(quality: 90, maxWidth: 1920) {
              ...GatsbyImageSharpFluid_noBase64
            }
          }
        }

        bg2:
        file(relativePath: { eq: "obj.jpg" }) {
          childImageSharp {
            fluid(quality: 90, maxWidth: 1920) {
              ...GatsbyImageSharpFluid_noBase64
            }
          }
        }

      }
    `
  )

  // const cdl_muros = React.lazy(() => import('../images/cdl_muros.obj'))
  

  // const obj = useLoader(OBJLoader, '../images/cdl_muros.obj')

  // const data2 = useStaticQuery(
  //   graphql`
  //     query {
  //       desktop: file(relativePath: { eq: "obj.jpg" }) {
  //         childImageSharp {
  //           fluid(quality: 90, maxWidth: 1920) {
  //             ...GatsbyImageSharpFluid_noBase64
  //           }
  //         }
  //       }
  //     }
  //   `
  // )

  const bg1 = data.bg1.childImageSharp.fluid
  const bg2 = data.bg2.childImageSharp.fluid

  const modalRef = useRef(null);

  const iframeRef = useRef(null);
  const itemTitulo = useRef(null);
  const itemFecha = useRef(null);
  const itemMedio = useRef(null);
  const itemFuente = useRef(null);

  // const showModal = (link) => {
  //   console.log(link);
  //   // let modal = new Modal(document.getElementById('myModal'), {});
  //   // document.getElementById('myModal').show();
  //   // jQuery('#myModal').modal()
  // }

  let window = window || {};

  const DrawTable = () => {

    const [data,setData] = useState([]);

    fetch(
      "https://opensheet.elk.sh/1HnOngNCBpJHBp2_GQbo7X_83dDXW-QMdMw-ybb7oaVA/Prensa"
    )
      .then((res) => res.json())
      .then((data) => {
        setData(data);
      });

      return (<div className={'table-responsive'}><table className={'table table-striped table-sm'}><tbody>

            <tr>
              <td>Autor</td>
              <td>Titulo</td>
              <td>Medio</td>
              <td>Fecha</td>
              <td>URL</td>
            </tr>

        {data.map((val, key) => {
          return (
            <tr key={key}>
              <td>{val['Autor']}</td>
              <td>{val['Ttulo']}</td>
              <td>{val['Medio']}</td>
              <td>{val['Fecha (Da, mes ao)']}</td>
              <td>{ val['URL'] ? <a role='button' href={'#'} onClick={ () => {iframeRef.current.src=val['URL']; itemTitulo.current.innerHTML=val['Ttulo']; itemMedio.current.innerHTML=val['Medio']; itemFecha.current.innerHTML=val['Fecha (Da, mes ao)']; itemFuente.current.innerHTML=val['Fuente']; }} data-bs-toggle="modal" data-url={val['URL']} data-bs-target="#myModal">Ver</a> : '' }</td>
            </tr>
          )
        })}     
      </tbody></table></div>);
  }

  const DrawChart = () => {

    d3.textwrap = textwrap;

    const [treeDataSource,setTreeDataSource] = useState([]);
    const svgRef = useRef(null);
    const svgWidth = window.innerWidth-200;
    const svgHeight = window.innerHeight*1.5;

    var wrap = d3.textwrap().bounds({height: 50, width: 200, x: 15, y: -15});

    useEffect( () => {
      // drawTree();

      new Promise( (res, rej) => {
        fetch("https://opensheet.elk.sh/1HnOngNCBpJHBp2_GQbo7X_83dDXW-QMdMw-ybb7oaVA/Prensa")
          .then((res) => res.json())
          .then((data) => {

            // console.log(data);
            let parsedTreeData = data.map( item => {
                return {'name': item['Ttulo']}
              });

            // setTreeDataSource({'name': 'Prensa', 'children': parsedTreeData})
            // console.log(treeDataSource);
            res({'name': 'Prensa', 'children': parsedTreeData})
            
          });
      }).then( (res) => {
        drawTree(res);
      })

    // let sheetData = fetch(
    //   "https://opensheet.elk.sh/1HnOngNCBpJHBp2_GQbo7X_83dDXW-QMdMw-ybb7oaVA/Prensa"
    // )
    //   .then((res) => res.json())
    //   .then((data) => {

    //     // console.log(data);

    //     let parsedTreeData = data.map( item => {
    //         return {'name': item['Ttulo']}
    //       });

    //     setTreeDataSource({'name': 'Prensa', 'children': parsedTreeData})
    //     // console.log(treeDataSource);

    //     setTimeout( () => {
    //       console.log(treeDataSource);
    //       drawTree();
    //     }, 5000)
        
    //   });


      // modalRef.current.addEventListener('show.bs.modal', function (event) {
      //   console.log(event);
      // })

    }, [])  

    const drawTree = (resData) => {

      console.log('render');

      let height = svgHeight;
      let width = svgWidth;
      
      let data = d3.hierarchy(resData)
      // const svg = d3.create("svg");
      const svg = d3.select(svgRef.current);

      let diameter = height * 0.75;
      let radius = diameter / 2;let tree = d3.tree()
          .size([2 * Math.PI, radius])
          .separation(function(a, b) { 
              return (a.parent == b.parent ? 1 : 2) / a.depth; 
          });

      let treeData = tree(data);

      let nodes = treeData.descendants();
      let links = treeData.links();

      let graphGroup = svg.append('g').attr('transform', "translate("+(width/2)+","+(height/2)+")");

      graphGroup.selectAll(".link")
        .data(links)
        .join("path")
        .attr("class", "link")
        .attr("d", d3.linkRadial()
          .angle(d => d.x)
          .radius(d => d.y));

      let node = graphGroup
              .selectAll(".node")
              .data(nodes)
              .join("g")
              .attr("class", "node")
              .attr("transform", function(d){
                  return `rotate(${d.x * 180 / Math.PI - 90})` 
                      + `translate(${d.y}, 0)`;
              });        

      node.append("circle").attr("r", 3);

      node.append("text")
        .attr("font-family", "sans-serif")
        .attr("font-size", 12)
        .attr("width", 100)
        .attr("dx", function(d) { return d.x < Math.PI ? 8 : -8; })
        .attr("dy", ".25em")
        .attr("text-anchor", function(d) { 
            return d.x < Math.PI ? "start" : "end"; 
        })
        .attr("transform", function(d) { 
            return d.x < Math.PI ? null : "rotate(180)"; 
        })
        .text( function (d) {
          return d.data.name;
          // let dArray = d.data.name.split(" ");
          // console.log(dArray);
          // if (!(dArray.length > 1)) {
            // return d.data.name;
          // } else {
            // for (let i = 0; i < dArray.length ; i++) {

              // this.append("tspan").text(dArray[i]+" ");
              // return <tspan>dArray[i]</tspan>
            // }            
          // }
        })
        // .text(function(d) { 
          // let dArray = d.data.name.split(" ");
          // return d.data.name;
        // });


        // node.select("text")
        //   .append(function (d) {
        //     let dArray = d.data.name.split(" ");
        //     console.dir(dArray);
        //     // dArray.forEach((item) => {
        //       // return <tspan></tspan>
        //     // });
        //   });

      d3.selectAll('text').call(wrap);


    }


    // useEffect( () => {
    //   // console.dir(data);
    // }, [data]);

    

    


    return (<svg ref={svgRef} width={svgWidth} height={svgHeight} />);

  }

  return (

      <Layout>

        <Seo title="Home" />

        <FullScreenSection>

          <BackgroundImage
            Tag="section"
            className={'container-fluid fullheight-ribbon d-flex flex-column'}
            fluid={bg1}
            backgroundColor={`#040e18`}
          >

          	<div className={`row flex-1 pt-3`}>
        		        		
              <div className={`col h-100`}>

                <h1 className={`text-black display-4`}>Repositorio</h1>
                <p className={`text-black`}>Compendio de archivos de video, audio, fotografas y documentos que se relacionen con el mural del Paso Inferior Santa Luca en un slo lugar y que puedan servir para orientar a profesores e investigadores de artes visuales, arquitectura, urbanismo y pblico general con respecto al arte en espacio pblico.</p>

                <div className={`text-center`}>
                  <StaticImage
                    alt={`Curva de la Luz`}
                    src="../images/repositorio.png"
                    width={300}
                    quality={95}
                    style={{ 
                    }}
                  />                  
                </div>

                <div className={`mt-3 rounded-3 p-5 mb-5`} style={{ position: "relative", width: '100%', backgroundColor: 'rgba(0, 91, 172, 1)' }}>
                  <DrawTable />
                  <DrawChart />
                </div>
                

              </div>

          	</div>

            <div className={`row ribbon no-gutters`}>

            	<div className={`col-3 ribbon-blue`}></div>
            	<div className={`col-2 ribbon-red`}></div>
            	<div className={`col-4 ribbon-navy`}></div>
            	<div className={`col-3 ribbon-gray`}></div>

            </div>

          </BackgroundImage>

        </FullScreenSection>

        <div ref={modalRef} className={"modal fade"} id="myModal" data-bs-backdrop="static" data-bs-keyboard="false" tabIndex={-1} aria-labelledby="staticBackdropLabel" aria-hidden="true">
          <div className={"modal-dialog"}>
            <div className={"modal-content"}>
              <div className={"modal-header"}>
                <h5 className={"modal-title"} id="staticBackdropLabel"></h5>
                <button type="button" className={"btn-close"} data-bs-dismiss="modal" aria-label="Cerrar"></button>
              </div>
              <div className={"modal-body"}>
                 <img ref={iframeRef} />
                 <p ref={itemTitulo} className={"modal-body-item-titulo p-0 m-0 fs-4"}></p>
                 <p className={"modal-body-item-medio p-0 m-0 fs-5"}><span ref={itemMedio}></span><span>, </span><span ref={itemFecha}></span></p>
                 <p ref={itemFuente} className={"modal-body-item-fuente p-0 m-0 fs-6"}></p>
              </div>
            </div>
          </div>
        </div>
      
      </Layout>
    
  )

}

export default RepositorioPage;
